import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { List } from '../models/List';
import { Observable } from 'rxjs';
// import { readFileSync } from '';

const httpOptions = {
  headers: new HttpHeaders({
    'Content-Type': 'application/json'
  })
}
@Injectable({
  providedIn: 'root'
})
export class ListService {

  listsURL:string = 'http://localhost:3000/users';
  listsLimit = '?_limit=2';
  

  constructor(private http:HttpClient) { }

  // Get List
  getLists():Observable<List[]>{
    return this.http.get<List[]>(`${this.listsURL}${this.listsLimit}`);
  }

  // Toggle Completed
  toggleCompleted(list: List):Observable<any> {
    const url = `${this.listsURL}/${list.id}`;
    return this.http.put(url, list, httpOptions);
  }

  // Delete List
    deleteList(list:List):Observable<List>{
      const url = `${this.listsURL}/${list.id}`;
      return this.http.delete<List>(url, httpOptions);
  }

  // Add Lists
  addList(list:List):Observable<List>{
    // var fs = require('fs');
    // var data = fs.readFileSync(new.json);
    // var words = JSON.parse(data);
    return this.http.post<List>(this.listsURL, list, httpOptions);
  }

  // Loop
//  loopList(list:List):Observable<List>{
//    
//    while(list.id!<2)
//    {}
//  }
}
